<template>
  <div class="public">
    <header-content></header-content>
    <div class="public-content">
      <left :curid="curid" :isPull="isPull" @pull="pull"></left>
      <div class="right">
        <div class="right-content">
          <!-- 头部 -->
          <div class="title">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shouye" />
            </svg>
            <p>
              <span>
                <router-link :to="curURL">{{curName}}管理</router-link>
              </span> /
              <span>{{title}}</span>
            </p>
          </div>
          <!-- 详情 -->
          <div class="info">
            <el-form :model="info" :rules="rules" ref="info" label-width="180px" class="pro-info">
              <el-form-item :label="curName+'名称：'" prop="name">
                <el-input v-model="info.name"></el-input>
              </el-form-item>
              <el-form-item :label="curName+'封面：'" prop="img">
                <el-upload
                  class="avatar-uploader"
                  name="imgfile"
                  :action="RespondURL.url+'Goodsparticulars/uploadimg'"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                >
                  <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  <div v-if="imageUrl" class="delImg" @click.stop="imageUrl='';info.img=''">
                    <i class="el-icon-circle-close"></i>
                  </div>
                  <div v-if="imageUrl" class="revise">
                    替换
                    <i class="el-icon-copy-document"></i>
                  </div>
                </el-upload>
              </el-form-item>

              <el-form-item label="获得时间: " prop="time">
                <el-date-picker
                  type="date"
                  placeholder="获得时间"
                  v-model="info.time"
                  @change="dateChange"
                ></el-date-picker>
              </el-form-item>
            </el-form>

            <div class="btn">
              <el-button type="primary" @click="submitForm">立即发送</el-button>
              <el-button @click="resetForm('info')">取消重置</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderContent from "@/components/HeaderContent.vue";
import Left from "@/components/Left.vue";
export default {
  name: "Product",
  components: { Left, HeaderContent },
  created() {
    if (this.$route.query.classify == 1) {
      this.title = "添加荣誉";
      this.curName = "荣誉";
      this.curid = "3";
      this.curURL = "/honor";
    } else {
      this.title = "添加专利";
      this.curName = "专利";
      this.curid = "2";
      this.curURL = "/patent";
    }
    if (this.$route.query.id) {
      if (this.$route.query.classify == 1) {
        this.title = "荣誉修改";
      } else {
        this.title = "专利修改";
      }
      let num = this.$route.query.id;
      this.stateArr.forEach((val, index) => {
        if (val == this.$store.state.arr[num].state) {
          this.$store.state.arr[num].state = index;
        }
      });
      this.info = {
        name: this.$store.state.arr[num].name,
        time: this.$store.state.arr[num].time,
        img: this.$store.state.arr[num].img,
        state: this.$store.state.arr[num].state,
        classify: this.$store.state.arr[num].classify
      };
      this.id = this.$store.state.arr[num].id;
      this.imageUrl = this.RespondURL.url + this.$store.state.arr[num].img; //图片路径
    }
  },
  data() {
    return {
      curid: "",
      curName: "",
      curURL: "",
      stateArr: ["下架", "上架"], // 状态
      title: "",
      id: "",
      info: {
        name: "",
        time: "",
        img: "",
        state: "",
        classify: ""
      },
      rules: {
        name: [{ required: true, message: "请输入专利名称", trigger: "blur" }]
      },
      imageUrl: "",
      isPull: false
    };
  },
  methods: {
    // 下拉
    pull() {
      if (this.isPull) {
        this.isPull = false;
      } else {
        this.isPull = true;
      }
    },
    handleAvatarSuccess(res) {
      this.imageUrl = this.RespondURL.url + res.result;
      this.info.img = res.result;
    },
    // 时间
    dateChange(data) {
      let date = this.$moment(data).format("YYYY-MM-DD");
      this.info.time = date;
    },
    // 发送
    submitForm() {
      this.$refs.info.validate(valid => {
        if (valid) {
          let repURL = "";
          if (this.id == "") {
            if (this.$route.query.classify == 1) {
              repURL = this.RespondURL.url + "HonorsAndPatents/insertHonors";
            } else {
              repURL = this.RespondURL.url + "HonorsAndPatents/insertPatents";
            }
          } else {
            repURL =
              this.RespondURL.url + "HonorsAndPatents/updateHonorsAndPatents";
            this.info.id = this.id;
          }
          this.$ajax.post(repURL, this.info).then(data => {
            this.$alert(data.data.describe, "提示", {
              confirmButtonText: "确定",
              callback: () => {
                if (this.$route.query.classify == 1) {
                  this.$router.push("/honor");
                } else {
                  this.$router.push("/patent");
                }
              }
            });
          });
        } else {
          this.$alert("发送失败，请内容填写完整", "提示", {
            confirmButtonText: "确定",
            callback: () => {}
          });
          return false;
        }
      });
    },
    // 重置
    resetForm() {
      this.info.name = "";
      this.info.time = "";
      this.info.img = "";
      this.imageUrl = "";
    }
  }
};
</script>
<style lang="less">
.content {
  text-align: left;
  .el-form-item__content {
    line-height: 25px;
    padding: 0 20px;
    .insert {
      background: #409eff;
      border-radius: 5px 5px 0 0;
      width: 80%;
      min-width: 500px;
      padding: 0 30px;
      .el-button {
        line-height: 30px;
        padding: 0;
        margin: 0;
        background: #409eff;
        border: none;
        font-size: 30px;
        color: white;
      }
    }
    .edit {
      width: 80%;
      min-width: 500px;
      height: 500px;
      overflow-y: scroll;
      padding: 20px;
      border: 1px solid rgb(196, 195, 195);
      outline: none;
      text-indent: 28px;
      img {
        display: block;
      }
    }
  }
}
</style>